#nav{
    height:40px;
    background:#068EFF;
    display: flex;
    justify-content: space-between;
    align-items:center;
    overflow: hidden;
}
.navbar{
    display:flex;
    box-sizing: border-box;
}
.navbar>li{
    padding:10px 20px;
    box-sizing: border-box;
    
    border-radius: 5px;
}
.navbar>li:hover{
    background: #00FF8C;
    
    cursor: default;
    
}
.li1Active{background: green;color:white;text-shadow: -1px -1px black} 


.navbar>li:active{
    box-shadow: 0 6px 6px grey inset;
}
#nav>div{
    margin-right: 20px;
    background: #00FF8C;
    border-radius: 5px;
    padding:10px 20px;
}