
ul,li,p,img{
    margin:0;
    padding:0;
}
#header{
    height:60px;
    width: 100%;
    text-align: center;
    position:relative;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
}
img.c_logo{
    height: 100%;
    width: 80px;
    
    transform: translateX(26px) scale(2)
}
img.t_logo{
    height: 100%;
    width: 200px;
    
    display: inline-block;
}
.p_right{
    
    font-size:10px; 
    display: flex;
    margin-right:5px; 
}

.p_right>div{
    border:1px solid grey;
    border-radius:2px;
    display: flex;
   justify-content: space-around;
   padding: 2px;
   margin: 2px;
   height: 24px;
   
   background: #EAEAEA;
   margin-top: 18px;
}
.p_right>div:hover{
    cursor:pointer;
}

img.p_right_xia_img{
    height:10px;
    display: inline-block;
    margin-top:3px; 
    margin-right:2px; 
}



