#secAside{
    background:#CCCFE2;
    width:20%;
}
#secAside>div{
    display: none;
}
#secAside>div:nth-child(1){
    display: block;
}
.scoreList>li{
    padding:10px;
    display: flex;
    align-items: center;
    border-radius:5px;
}

.scoreList>li:hover{
    background: rgb(155, 154, 154);
    cursor: pointer;
}
.scoreList>li:active{
    box-shadow: 0 6px 6px gray;
}
.scoreList>li>img{
    height: 16px;
    margin-right:5px;
}