/*例子A按钮*/
.demo1{
    padding: 10px;
    background: rgba(48, 69, 189, 0.685);
    width: 80px;
    text-align: center;
    color:white;
    margin-left: 14px;
    
}
/*第二行包含查询语句按钮、文本框、刷新的总容器*/
div.searchBox{
    margin-top: 10px;
    display: flex;
    align-items: center;
    margin-left:1.4%;
    margin-bottom:20px; 
}
/*查询语句按钮*/
div.btn{
    display:inline-block;
    width: 80px;
    padding:4px;
    background: rgb(189, 184, 184);
    text-align: center;
    border:1px solid gray;
    height:28px;
    border:none;
}

/*查询语句文本框*/
.searchBox>input{
    height:28px;
    margin-right: 20px;
}

/*刷新按钮*/
button.reload{
    height:28px;
    width: 160px;
    background: rgb(36, 80, 224);
    border-radius: 2px;
    border:1px solid rgb(36, 80, 224);
    color:white;
    
}

button.reload:active{
    transform: scale(1.1);
    opacity: 0.2;
    transition:0.4s;
    box-shadow: 0 6px 6px gray;
}
/*table表的外层容器*/
.tableBox{
 
    
     height: 200px;
     width: 96%;
     border:2px solid rgba(139, 136, 136,0.2);
     background:rgb(236, 232, 232);
     margin: auto;
     padding: 5px;
     box-shadow: 0 6px 10px gray;
     border-radius: 2px;
     
     
}
/*table表*/
.userTable{
    border-collapse:collapse;
    margin: auto;
    width: 100%;
}
/*tbody的奇数行*/
.userTable>tbody>tr:nth-of-type(odd){
    background: rgb(211, 211, 211);
    box-shadow: 0 0 3px gray inset;
}
th,td{
    border:1px solid rgba(148, 144, 144,0.8);
    width: 100px;
    font-size: 12px;
    border-radius: 2px;
    height: 26px;
    font-size:12px; 
    
}