#paging{
    display: flex; 
    align-items: center;
    justify-content: flex-end;
    height: 32px;
    
    margin-top:20px;
    
}

#paging>div:nth-child(1)>span{
    display: inline-block;
    width:32px;
    height:32px;
    line-height: 32px;
    text-align:center;
    box-sizing: border-box;
    border:1px solid gray;
}
#paging>div:nth-child(1)>span:nth-child(1){
    border-bottom-left-radius:5px;
    border-top-left-radius:5px;
}

#paging>div:nth-child(1)>span:nth-child(1):hover,#paging>div:nth-child(1)>span:nth-child(3):hover{
    cursor: pointer;
    user-select: none;
}
#paging>div:nth-child(1)>span:nth-child(2){
    border-left: none;
    border-right:none;
}
#paging>div:nth-child(2){
    width: 100px;
    height:32px;
    border:1px solid gray;
    text-align: center;
    border-left: none;
    border-right:none;
}
#paging>div:nth-child(3){
    width: 100px;
    height:32px;
    border:1px solid gray;
    text-align: center;
}
#paging>div:nth-child(3)>input{
    width: 32px;
    height:30px;
    line-height: 32px;
    text-align: center;
    
}
#paging>div:nth-child(4){
    border:1px solid gray;
    border-left: none;
    height:32px;
    line-height: 32px;
    border-bottom-right-radius:5px;
    border-top-right-radius:5px;
}




