#edit{
    display:none;
    background: rgba(0,0,0,0.4);
    
}

div.editContainer{
    width:60%;
    height: 280px;
    box-shadow: 0 0px 10px black;
    background: rgb(221, 221, 241);
    margin: 50px auto;
    border-radius:5px;
    padding: 5px;
}
div.editContainer>div{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height:20%;
}

div.editContainer>div:nth-child(1){
    justify-content:space-between;
}
div.editContainer>div:nth-child(1)>span{
    display: inline-block;
}


div.editContainer>div:nth-child(1)>span:nth-child(1){
    width: 80px;
    height:30px;
    line-height: 30px;
    margin-left: 10px;
}
div.editContainer>div:nth-child(1)>span:nth-child(1)>span{
    transform: translateY(-2px);
    display: inline-block;
}

div.editContainer>div:nth-child(1)>span:nth-child(2){
    width: 20px;
    height:20px;
    margin-right: 10px;
}
div.editContainer>div:nth-child(1)>span:nth-child(2):hover{
    background: rgb(167, 167, 167);
    
}
div.editContainer>div:nth-child(1)>span>img{
    
    height:100%;
    vertical-align: middle;
    margin-bottom: 8px;
    
}
div.editContainer>div:nth-child(5){
    justify-content:center;
}
div.editContainer>div:nth-child(5)>button{
    margin: 0 30px;
}

div.editContainer>div>div{
    
    position: relative;
    width: 40%;
    
    height: 100%;
    
}
div.editContainer>div>div>span{
    position:absolute;
    top:25%;
    right: 56%;
}
div.editContainer>div>div>input{
    position:absolute;
    top:25%;
    left: 50%;
    border-radius: 2px;
    border:1px solid rgb(167, 166, 166);
    height: 24px;
}
p.p2{
    position:absolute;
    top:10%;
    left: 50%;
}

div.editContainer>div>div:nth-child(even){
    
    margin-right: 100px;
}

button.cancel,button.submit{
    width: 100px;
    height:40px;
    border-radius:2px;
    border:none;
}
button.cancel{
    background:gray;
    color: rgb(43, 43, 43);
}
button.cancel:hover{
    background:rgb(87, 87, 87);
    color: black;
}
button.submit{
    background: rgb(83, 124, 238);
    color:white;
}