.usdBox{
    width:100%;
    
}

.radioGroup,.present{
    margin-left:2%;
    margin-top:20px; 
    margin-bottom:20px;
}



.present>span{
    display:inline-block;
    
    padding:0px 4px 6px;
    background: rgb(189, 184, 184);
    text-align: center;
    
    height:26px;
    
}

.present>input{
    height:26px;
    margin-right: 20px;
    transform: translateY(2px)
}

.usdSearch,.usdPresent{
    width:160px;
    border:none;
    margin: 0 10px;
    border-radius: 2px;
    color:white;
    height:26px;
}
.usdSearch{
    background: rgb(48, 48, 218);
}

.usdPresent{
    background: rgb(73, 123, 230);
}

.usdSearch:active,.usdPresent:active{
    transform: scale(1.1);
    opacity: 0.2;
    transition:0.4s;
    box-shadow: 0 6px 6px gray;
}

